.input::-webkit-input-placeholder       {opacity: 1; transition: opacity 0.3s ease; font-family: armalite, serif;}
.input::-moz-placeholder                {opacity: 1; transition: opacity 0.3s ease;     font-family: armalite, serif;}
.input:-moz-placeholder                 {opacity: 1; transition: opacity 0.3s ease;    font-family: armalite, serif;}
.input:-ms-input-placeholder            {opacity: 1; transition: opacity 0.3s ease;    font-family: armalite, serif;}
.input:focus::-webkit-input-placeholder {opacity: 0; transition: opacity 0.3s ease;    font-family: armalite, serif;}
.input:focus::-moz-placeholder          {opacity: 0; transition: opacity 0.3s ease;    font-family: armalite, serif;}
.input:focus:-moz-placeholder           {opacity: 0; transition: opacity 0.3s ease;    font-family: armalite, serif;}
.input:focus:-ms-input-placeholder      {opacity: 0; transition: opacity 0.3s ease;    font-family: armalite, serif;}


html {
    height: 100%;
    width: 100%;
}

body {
    overflow: hidden;
    height: 100%;
    width: 100%;
}
#body {
    height: 100%;
    width: 100%;
    /*background-color: magenta;*/
    /*background-image: url("/map.png");*/
    /*filter: blur(5px);*/
    /*background-repeat: no-repeat;*/
}
.start {
    height: 100%;
}

#radar-wrapper {
    width: 60%;
    background-color: #111111;
    margin: 0;
    position: absolute;
    height: 100%;
    display: inline-block;
    clear: both;
    float: left;
}
#application-wrapper {
    width: 40%;
    margin: 0;
    position: relative;
    display: inline-block;
    clear: both;
    float: right;
    height: 100%;
    padding: 0;
}



#application {
    width: 95%;
    margin-top: 1%;
    margin-left: 3%;
}

/*.button {*/
    /*height: 100px;*/
