    /*border: seagreen;*/
    /*background: #c91919;*/
/*}*/

.button a {
    display: block;
    margin-top: 10px;
    text-decoration: none;
    color: black;
    height: 100%;
    font-size: 1.5vw;
    /*padding:16px 18px 14px;*/
}

@media (max-aspect-ratio: 7/5 ) {
    #radar-wrapper #radar {
        display: none;
    }

    #application-wrapper.body {
        width: 98%;
    }

    .planck {
        width:70% ;
    }

    .button a {
        font-size: 3vw;
    }
}



@font-face {
    font-family: armalite;
    src: local(armalite),
    url(/fonts/armalite.ttf);
}


li {
    margin: 1%;
    margin-left: 0;
    margin-right: 0;
}

h2 {
    font-family: armalite, serif;
    color: white;
    font-size: 300%;
    margin: 2%;
}
p {
    font-family: armalite, serif;
}

.form-wrapper {
    text-align: center;
}

#game-canvas {
    /*border:  solid;*/
}

.overlay {
    background-color: rgba(100,100,100,0.5);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
}

.attack-animation {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
}

