/*Бэкграунд*/
.body {
    background: #818181; /* Old Browsers */
    background: -webkit-radial-gradient(top,#818181,#3d3d3d); /*Safari 5.1-6*/
    background: -o-radial-gradient(top,#818181,#3d3d3d); /*Opera 11.1-12*/
    background: -moz-radial-gradient(top,#818181,#3d3d3d); /*Fx 3.6-15*/
    background: radial-gradient(to bottom, #818181, #3d3d3d); /*Standard*/
    background-repeat: no-repeat;
    /*overflow-x: hidden;*/
    /*min-width: 100%;*/
    height: 100%;
    /*position: relative;*/
    border: 8px groove #646464;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
}

